body {
  font-family: 'Steam';
}

header {
  background-color: #272727;
}

.navbar {
  padding: 3rem 0rem;
}

.header-heading {
  font-family: 'Steam Medium';
  font-weight: 500;
  letter-spacing: -.13px;
  color: #fff;
}

.header-heading .brand {
  letter-spacing: -.13px;
  color: #ffe73e;
}

.header-heading .animation {
  color: #db39ed;
}

.header-heading .logos {
  color: #ed394b;
}

.header-heading .presentations {
  color: #4bed39;
}

.nav-item {
  font-family: 'Steam';
  font-size: 24px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: -.03px;
  text-transform: lowercase;
}

.navbar-brand {
  font-family: 'Steam';
  font-size: 24px;
  font-weight: 400;
  margin-right: 0;
  letter-spacing: -.3px;
  text-transform: lowercase;
}

.button-rounder {
  padding-top: 8px;
  padding-right: 61px;
  padding-bottom: 7px;
  padding-left: 53px;
  cursor: pointer;
  border: 1px solid rgba(32, 32, 32, 0.5);
  border-radius: 22px;
  background: transparent;
}

.button-rounder-white {
  width: 222px;
  height: 43px;
  cursor: pointer;
  border: 1px solid rgba(32, 32, 32, 0.5);
  border-color: #fff;
  border-radius: 22px;
  background: transparent;
}

.panda-block {
  background: url("../img/panda.png") no-repeat center center;
  background-size: cover;
}

.fragmanu-block {
  background: url("../img/fragmanu.png") no-repeat center center;
  background-size: cover;
}

.client-block {
  background: url("../img/Cucina.png") no-repeat center center;
  background-size: cover;
}

.footer-text {
  font-family: 'Steam Light';
  font-size: 18px;
  font-weight: 300;
  letter-spacing: -.03px;
  opacity: .5;
  color: #303439;
}

.contact-text {
  font-family: 'Steam';
  font-weight: 400;
  letter-spacing: -.4px;
  color: #303439;
}

.contact-title {
  font-family: 'Steam';
  font-size: 18px;
  font-weight: 400;
  letter-spacing: -.03px;
  text-transform: uppercase;
  color: #798ea5;
}

.list-text {
  font-family: 'Steam Light';
  font-size: 18px;
  font-weight: 300;
  letter-spacing: -.3px;
  color: #303439;
}

.padding-block {
  padding: 155px;
  padding-left: 75px;
}

hr {
  box-sizing: border-box;
  height: 1px;
  border: 1px solid #ddd;
}

.social-list {
  list-style-type: none;
}

.social-text {
  font-family: 'Steam';
  font-size: 24px;
  font-weight: 700;
  letter-spacing: -.03px;
}

@media (min-width: 544px) {
  .header-heading {
    font-size: 30px;
    line-height: 30px;
  }
  .contact-text {
    font-size: 15px;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .header-heading {
    font-size: 65px;
    line-height: 65px;
  }
  .contact-text {
    font-size: 20px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .header-heading {
    font-size: 70px;
    line-height: 70px;
  }
  .contact-text {
    font-size: 25px;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .header-heading {
    font-size: 90px;
    line-height: 90px;
  }
  .contact-text {
    font-size: 30px;
  }
}
